import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      // 一级设置：上传渠道
      channels: [{
        value: 'telegram',
        label: 'Telegram'
      }, {
        value: 'cfr2',
        label: 'CloudFlareR2'
      }, {
        value: 's3',
        label: 'S3'
      }],
      activeChannel: 'telegram',
      // 当前选中的上传渠道

      // 二级设置：Telegram 配置
      telegramSettings: {
        loadBalance: {},
        channels: []
      },
      // 二级设置：CFR2 配置
      cfr2Settings: {
        channels: []
      },
      // 二级设置：S3 配置
      s3Settings: {
        loadBalance: {},
        channels: []
      }
    };
  },
  computed: {
    ...mapGetters(['credentials']),
    // 当前选中渠道的标签
    activeChannelLabel() {
      const channel = this.channels.find(item => item.value === this.activeChannel);
      return channel ? channel.label : '';
    }
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // 开发环境, url 前面加上 /api
      // url = `/api${url}`;
      if (this.credentials) {
        // 设置 Authorization 头
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // 确保包含凭据，如 cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    addChannel() {
      switch (this.activeChannel) {
        case 'telegram':
          this.telegramSettings.channels.push({
            id: this.telegramSettings.channels.length + 1,
            name: '',
            type: 'telegram',
            savePath: 'database',
            botToken: '',
            chatId: '',
            enabled: true,
            fixed: false
          });
          break;
        case 'cfr2':
          // this.cfr2Settings.channels.push({
          //     id: this.cfr2Settings.channels.length + 1,
          //     name: '',
          //     type: 'cfr2',
          //     savePath: 'database',
          //     enabled: true,
          //     fixed: false
          // });
          this.$message.error('暂不支持增加 R2渠道 配置');
          break;
        case 's3':
          this.s3Settings.channels.push({
            id: this.s3Settings.channels.length + 1,
            name: '',
            type: 's3',
            savePath: 'database',
            accessKeyId: '',
            secretAccessKey: '',
            region: '',
            bucketName: '',
            endpoint: '',
            enabled: true,
            fixed: false
          });
          break;
      }
    },
    deleteChannel(index) {
      switch (this.activeChannel) {
        case 'telegram':
          // 调整 id
          this.telegramSettings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.telegramSettings.channels.splice(index, 1);
          break;
        case 'cfr2':
          // 调整 id
          this.cfr2Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.cfr2Settings.channels.splice(index, 1);
          break;
        case 's3':
          // 调整 id
          this.s3Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.s3Settings.channels.splice(index, 1);
          break;
      }
    },
    saveSettings() {
      const settings = {
        telegram: this.telegramSettings,
        cfr2: this.cfr2Settings,
        s3: this.s3Settings
      };
      this.fetchWithAuth('/api/manage/sysConfig/upload', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(settings)
      }).then(() => {
        this.$message.success('设置已保存');
      });
    }
  },
  mounted() {
    // 获取上传设置
    this.fetchWithAuth('/api/manage/sysConfig/upload').then(response => response.json()).then(data => {
      this.telegramSettings = data.telegram;
      this.cfr2Settings = data.cfr2;
      this.s3Settings = data.s3;
    });
  }
};